<template>
  <div class="TakeActivity_container">
    <div class="TAcontainer_header">
      <span @click="goBack">返回</span>
      <span>我参加的活动</span>
    </div>
    <div class="TAcontainer_mainer" v-if="total!=0">
      <el-table
        :data="
          tableData.filter(
            (data) =>
              !search || data.name.toLowerCase().includes(search.toLowerCase())
          )
        "
        height="400"
        style="width: 100%; font-size: 18px"
      >
        <el-table-column
          label="日期"
          prop="date"
          width="200"
          align="center"
          show-overflow-tooltip
          sortable
        >
        </el-table-column>
        <el-table-column
          label="活动地址"
          prop="date"
          width="300"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          label="活动名称"
          prop="date"
          width="250"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          align="center"
          prop="tag"
          label="状态"
          width="150"
          :filters="[
            { text: '报名中', value: '报名中' },
            { text: '已结束', value: '已结束' },
            { text: '进行中', value: '进行中' },
          ]"
          :filter-method="filterTag"
          filter-placement="bottom-end"
        >
          <template slot-scope="scope">
            <el-tag :type="tagStyle(scope.row.tag)" disable-transitions>{{
              scope.row.tag
            }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label="报名人数"
          prop="name"
          width="250"
          show-overflow-tooltip
          align="center"
        >
        </el-table-column>
        <el-table-column align="right">
          <template slot="header">
            <el-input
              v-model="search"
              size="medium"
              placeholder="输入关键字搜索"
            />
          </template>
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
              >查看详情</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              >删除记录</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage1"
      :page-size="8"
      style="text-align:center"
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>
    </div>
      <div class="null_container" v-else>
        <img src="../../../static/images/department/null_icon.png" alt="请检查网络" title="无">
        <span>您还未参加任何活动呢
          <router-link to="department/watch-activity">点击参加</router-link>
        </span>
    </div>
  </div>
</template>

<script>
import {GetJoinList} from "../../api/data";
export default {
  data() {
    return {
      //数据
      tableData: [],
      search: "",
      currentPage1: 1,
      //条数
      total:''
    };
  },
  methods: {
    goBack() {
      this.$router.push({
        path: "/department/my-activity",
      });
    },
    handleEdit(index, row) {
      console.log(index, row);
    },
    handleDelete(index, row) {
      console.log(index, row);
    },
    filterTag(value, row) {
      return row.tag === value;
    },

     handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      }
  },
  computed: {
    tagStyle() {
      return (val) => {
        if (val === "已结束" || val==="已取消") {
          return "danger";
        } else if (val === "进行中") {
          return "primary";
        } else {
          return "success";
        }
      };
    },
  },
  async created() {
            let result=await GetJoinList({pageNum:1});
            this.tableData=result["data"].result.data;
            this.total=result["data"].result.total;
            console.log(result);
  },
};
</script>

<style lang="less" scoped>
@import "../../../static/less/deportment/takeactivity.less";
</style>


